<template>
  <div class="container py-5">
    <form class="w-100" @submit.prevent.stop="handleSubmit">
      <div class="text-center mb-4">
        <h1 class="h3 mb-3 font-weight-normal">Sign In</h1>
      </div>

      <div class="form-label-group mb-2">
        <label for="account">Account</label>
        <input
          id="account"
          v-model="account"
          name="account"
          type="text"
          class="form-control"
          placeholder="Account"
          autocomplete="current-account"
          required
        />
      </div>

      <div class="form-label-group mb-3">
        <label for="password">Password</label>
        <input
          id="password"
          v-model="password"
          name="password"
          type="password"
          class="form-control"
          placeholder="Password"
          autocomplete="current-password"
          required
        />
      </div>

      <button
        class="btn btn-lg btn-danger btn-border btn-block mb-3"
        :disabled="isProcessing"
        type="submit"
      >Submit</button>

      <!-- <p class="mt-5 mb-3 text-muted text-center">&copy; 2020</p> -->
    </form>
  </div>
</template>

<script>
import authorizationAPI from '../apis/authorization';
import { Toast } from '../utils/helpers';

export default {
  data() {
    return {
      account: '',
      password: '',
      isProcessing: false,
    };
  },
  methods: {
    async handleSubmit() {
      try {
        if (!this.account || !this.password) {
          Toast.fire({
            icon: 'warning',
            title: '請填入 account 和 password',
          });
          return;
        }
        this.isProcessing = true;
        const response = await authorizationAPI.signIn({
          account: this.account,
          password: this.password,
        });
        const { data } = response;
        if (data.status !== 'success') {
          throw new Error(data.message);
        }
        // 將伺服器回傳的 token 保存在 localStorage 中
        localStorage.setItem('token', data.token);
        // 透過 setCurrentUser 把使用者資料存到 Vuex 的 state 中
        this.$store.commit('setCurrentUser', data.user);
        // 成功登入後進行轉址
        this.$router.push('/heatmap');
      } catch (error) {
        this.isProcessing = false;
        this.password = '';
        console.error(error.message);
        // 顯示錯誤提示
        Toast.fire({
          icon: 'warning',
          title: '輸入的帳號密碼有誤 !',
        });
      }
    },
  },
};
</script>
