<template>
  <div class="container py-5">
    <NavTabs />
    Refresh
    <select>
      <option>5 秒</option>
    </select>
    <br/><br/>
    <div id="heatmap">
      <img src="../assets/dog-unsplash.jpg" style="width:100%; height: 100%">
    </div>
  </div>
</template>

<script>

import Heatmap from 'heatmap.js';
import eyetrackingAPI from '../apis/eyetracking';
import NavTabs from '../components/NavTabs.vue';

let heatmapInstance = '';

export default {
  name: 'heatmap',
  components: {
    NavTabs,
  },
  data() {
    return {
      timer: '',
      timerSecond: 5000,
    };
  },
  mounted() {
    console.log('mounted');
    heatmapInstance = Heatmap.create({
      container: document.getElementById('heatmap'),
    });
    this.getEyetrackingLocationCount();
    this.timer = setInterval(() => {
      this.getEyetrackingLocationCount();
    }, this.timerSecond);
  },
  methods: {
    async getEyetrackingLocationCount() {
      console.log('getEyetrackingLocationCount');
      const response = await eyetrackingAPI.getLocationCount();
      const { content } = response.data;
      // console.log(`getEyetrackingLocationCount_Response: ${JSON.stringify(content)}`);
      if (Object.keys(content).length) {
        console.log('have');
        const jsonKeys = Object.keys(content);
        const points = content[jsonKeys[0]];
        // console.log(`jsonArray: ${JSON.stringify(points)}`);
        const data = {
          max: 2,
          data: points,
        };
        heatmapInstance.setData(data);
      }
    },
  },
  beforeDestroy() {
    console.log('beforeDestroy');
    clearInterval(this.timer);
  },
};
</script>
