//   if (currentUser && !(currentUser.role == 'admin' || currentUser.role == 'super')) {
//     next('/not-found');
//     return;
//   }

//   next();
// }

const routes = [
  {
    path: '/',
    name: 'root',
    redirect: '/signin',
  },
  {
    path: '/signin',
    name: 'sign-in',
    component: SignIn,
  },
  {
    path: '/heatmap',
    name: 'heapmap',
    component: Heatmap,
  },
  {
    path: '/config',
    name: 'config',
    component: () => import('../views/Config.vue'),
  },
  {
    path: '*',
    name: 'not-found',
    component: NotFound,
  },
];

const router = new VueRouter({
  linkExactActiveClass: 'active',
  routes,
});

router.beforeEach(async (to, from, next) => {
  // 從 localStorage 取出 token
  const token = localStorage.getItem('token');
  const tokenInStore = store.state.token;

  let { isAuthenticated } = store.state;

  // 有 token 的情況下，才向後端驗證
  if (token && token !== tokenInStore) {
    isAuthenticated = await store.dispatch('fetchCurrentUser');
  }

  const pathsWithoutAuthentication = ['sign-in'];

  // 如果 token 無效，且要去除了登入和註冊以外的其他頁面，則轉址到登入頁
  if (!isAuthenticated && !pathsWithoutAuthentication.includes(to.name)) {
    next('/signin');
    return;
  }

  // 如果 token 有效，且要去登入和註冊頁，則轉址到餐廳論壇首頁
  if (isAuthenticated && pathsWithoutAuthentication.includes(to.name)) {
    next('/heatmap');
    return;
  }

  next();
});

export default router;
